/* =====================================================================================================================
// Skin details */

.irs {
  height: 0.733333rem;
}

.irs-with-grid {
  height: 1rem;
}

.irs-line {
  height: 0.053333rem;
  top: 0.44rem;
  background: #EEE;
  background: linear-gradient(to bottom, #DDD -50%, #FFF 150%);
  /* W3C */
}

.irs-line-left {
  height: 0.106667rem;
}

.irs-line-mid {
  height: 0.106667rem;
}

.irs-line-right {
  height: 0.106667rem;
}

.irs-bar {
  height: 0.053333rem;
  top: 0.44rem;
  background: #3B48EE;
  /*background: linear-gradient(to top, rgba(66,139,202,1) 0%,rgba(127,195,232,1) 100%); !* W3C *!*/
}

.irs-bar-edge {
  height: 0.04rem;
  top: 0.453333rem;
  width: 0.32rem;
  background: #3B48EE;
  display: none;
  /* background: linear-gradient(to top, rgba(66,139,202,1) 0%,rgba(127,195,232,1) 100%); !* W3C *!*/
}

.irs-shadow {
  height: 0.04rem;
  top: 0.506667rem;
  background: #000;
  opacity: 0.3;
  border-radius: 0.066667rem;
  -moz-border-radius: 0.066667rem;
}

.lt-ie9 .irs-shadow {
  filter: alpha(opacity=30);
}

.irs-slider {
  top: 0.133333rem;
  width: 0.64rem;
  height: 0.64rem;
  background: #FFFFFF;
  box-shadow: 0 0.026667rem 0.053333rem 0 rgba(0,0,0,0.20);
  border-radius: 50%;
  cursor: pointer;
}

.irs-slider.state_hover,
.irs-slider:hover {
  background: #FFF;
}

.irs-min,
.irs-max {
  font-family: SFProDisplay-Medium;
  color: #A8B4C4;
  font-size: 12px;
  line-height: 1.333;
  text-shadow: none;
  top: 0.8rem;
  padding: 0.013333rem 0.066667rem;
}

[data-dpr="2"] .irs-max {
  font-size: 24px;
}

[data-dpr="3"] .irs-max {
  font-size: 36px;
}

[data-dpr="2"] .irs-min {
  font-size: 24px;
}

[data-dpr="3"] .irs-min {
  font-size: 36px;
}

.lt-ie9 .irs-min,
.lt-ie9 .irs-max {
  background: #ccc;
}

.irs-from,
.irs-to,
.irs-single {
  color: #fff;
  font-size: 0.186667rem;
  line-height: 1.333;
  text-shadow: none;
  padding: 0.013333rem 0.066667rem;
  background: #428bca;
  border-radius: 0.04rem;
  -moz-border-radius: 0.04rem;
  display: none;
}

.lt-ie9 .irs-from,
.lt-ie9 .irs-to,
.lt-ie9 .irs-single {
  background: #999;
}

.irs-grid {
  height: 0.36rem;
}

.irs-grid-pol {
  opacity: 0.5;
  background: #428bca;
}

.irs-grid-pol.small {
  background: #999;
}

.irs-grid-text {
  bottom: 0.066667rem;
  color: #99a4ac;
}


