/* layer begin */

.ios-select-widget-box.olay {
  position: fixed;
  z-index: 500;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  background: rgba(0, 0, 0, 0.75);
}

.ios-select-widget-box.olay > div {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
  bottom: 0;
  left: 0;
  visibility: visible;
}

.ios-select-widget-box header.iosselect-header {
  height: 0.586667rem;
  line-height: 0.586667rem;
  background-color: #eee;
  width: 100%;
  z-index: 9999;
  text-align: center;
}

.ios-select-widget-box header.iosselect-header a {
  font-size: 16px;
  color: #e94643;
  text-decoration: none;
}

[data-dpr="2"] .ios-select-widget-box header.iosselect-header a {
  font-size: 32px;
}

[data-dpr="3"] .ios-select-widget-box header.iosselect-header a {
  font-size: 48px;
}

.ios-select-widget-box header.iosselect-header a.close {
  float: left;
  padding-left: 0.2rem;
  height: 0.586667rem;
  line-height: 0.586667rem;
}

.ios-select-widget-box header.iosselect-header a.sure {
  float: right;
  padding-right: 0.2rem;
  height: 0.586667rem;
  line-height: 0.586667rem;
}

