.ios-select-widget-box .cover-area1 {
  width: 100%;
  border: none;
  border-top: 0.013333rem solid #d9d9d9;
  position: absolute;
  top: 1.986667rem;
  margin: 0;
  height: 0;
}

.ios-select-widget-box .cover-area2 {
  width: 100%;
  border: none;
  border-top: 0.013333rem solid #d9d9d9;
  position: absolute;
  top: 2.44rem;
  margin: 0;
  height: 0;
}

.ios-select-widget-box #iosSelectTitle {
  margin: 0;
  padding: 0;
  display: inline-block;
  font-size: 16px;
  font-weight: normal;
  color: #333;
}

[data-dpr="2"] .ios-select-widget-box #iosSelectTitle {
  font-size: 32px;
}

[data-dpr="3"] .ios-select-widget-box #iosSelectTitle {
  font-size: 48px;
}

.ios-select-body-class {
  overflow: hidden;
}

.ios-select-body-class body {
  touch-action: none;
}

.ios-select-widget-box.olay > div > .ios-select-loading-box {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,.5);
  display: none;
}

.ios-select-widget-box.olay > div > .ios-select-loading-box > .ios-select-loading {
  width: 0.666667rem;
  height: 0.666667rem;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -0.333333rem;
  margin-left: -0.333333rem;
  background: url(data:image/png;base64,) no-repeat 0 0;
  background-size: contain;
  -webkit-animation: loading-keyframe 1s infinite linear;
  animation: loading-keyframe 1s infinite linear;
}

.fadeInUp .layer {
  -webkit-animation: fadeInUp .5s;
  animation: fadeInUp .5s;
}

.fadeOutDown .layer {
  -webkit-animation: fadeOutDown .5s!important;
  animation: fadeOutDown .5s!important;
}

@-webkit-keyframes loading-keyframe {
  from {
    -webkit-transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes loading-keyframe {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0,100%,0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0,100%,0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@-webkit-keyframes fadeOutDown {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
  }
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
}


